.Input {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
    
}

.Input {
    outline: none;
    border: 1px solid #ccc;
    background-color: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;
}

.Input:focus {
    outline: none;
    background-color: rgb(223, 222, 222);
}

.Invalid {
    border: 1px solid red;
    background-color: rgb(233, 118, 105);
    
}